import { useEffect } from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { v4 as uuidv4 } from 'uuid';

import {useHttp} from '../../hooks/http.hook';
import { useDispatch, useSelector } from 'react-redux';

import { addHero } from '../../actions';
import Spinner from '../spinner/Spinner'

//    :
//       .   
//        + 
//       uiid
//  :
//      json    POST
// :
//  <option></option>    
//   

const HeroesAddForm = () => {
    const dispatch = useDispatch()
    const {request} = useHttp();

    const {filters, filtersLoadingStatus} = useSelector(state => state)


    const createHero = ({name, text, element}, resetForm) => {
        const newHero = {
            id: uuidv4(),
            name: name,
            description: text,
            element: element
        }

        request("http://localhost:3001/heroes", "POST", JSON.stringify(newHero))
            .then(dispatch(addHero(newHero)))
                .then(resetForm())
    }

    const renderFilterOptions = (filters, status) => {
        if (status === 'loading') {
            return <option> </option>
        } else if (status === 'error') {
            return <option></option>
        }

        return filters.map(item => {
            if (item.name === 'all') {
                return
            }

            return <option value={item.name}>{item.text}</option>
        })
    }

    const elements = renderFilterOptions(filters, filtersLoadingStatus)

    return (
        <Formik
            initialValues={{
                name: '',
                text: '',
                element: ''
            }}
            validationSchema={Yup.object({
                name: Yup.string()
                        .min(2, ' 2 ')
                        .required(' '),
                text: Yup.string()
                        .min(10, ' 10 ')
                        .required(' '),
                element: Yup.string().required(' ')
            })}
            onSubmit={(values, {resetForm}) => createHero(values, resetForm)}
        >
            <Form className="border p-4 shadow-lg rounded">
                <div className="mb-3">
                    <label htmlFor="name" className="form-label fs-4">  </label>
                    <Field 
                        type="text" 
                        name="name" 
                        className="form-control" 
                        id="name" 
                        placeholder="  ?"
                    />
                    <ErrorMessage className='error' name="name" component="div"/>
                </div>

                <div className="mb-3">
                    <label htmlFor="text" className="form-label fs-4"></label>
                    <Field 
                        name="text" 
                        className="form-control" 
                        id="text" 
                        placeholder="  ?"
                        as="textarea"
                        style={{"height": '130px'}}
                    />
                    <ErrorMessage className='error' name="text" component="div"/>
                </div>

                <div className="mb-3">
                    <label htmlFor="element" className="form-label">  </label>
                    <Field
                        className="form-select" 
                        id="element" 
                        name="element"
                        as="select"
                    >
                        <option >  ...</option>
                        {elements}
                    </Field>
                    <ErrorMessage className='error' name="element" component="div"/>
                </div>

                <button type="submit" className="btn btn-primary"></button>
            </Form>
        </Formik>
    )
}

export default HeroesAddForm;